.card {
    border: 5px solid white;
    margin: 8px 20px;
    -webkit-box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
            box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
    z-index: 75;
}

.card:hover {
    cursor: pointer;
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-transition: .1s;
    -o-transition: .1s;
    transition: .1s;
    -webkit-box-shadow: 0 9px 18px rgba(0,0,0,.16), 0 9px 18px rgba(0,0,0,.23);
            box-shadow: 0 9px 18px rgba(0,0,0,.16), 0 9px 18px rgba(0,0,0,.23);
    z-index: 75;
}

.card>img {
    background-size: cover;
    /* background-position: 50%; */
    height: 170px;
    width: 170px;
    /* border: 5px solid #f1f1f1; */
    /* border-radius: 3px;    */
}  

.card>img:hover {
    /* box-shadow: 3px 6px 20px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23); */
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
}


